@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;500;700&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Inter', sans-serif;
}

.nav {
  background-color: #F55A5A;
  height: 55px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.navbar-title {
  color: #FFFFFF;
  font-size: 14.46px;
  font-weight: 500;
  padding-left: 7px;
}

main {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-top: 27px;
}

.journal-entry {
  display: flex;
  width: 550px;
  padding-bottom: 18px;
  border-bottom: 1px solid #F5F5F5;
  margin-top: 18px;
}

.journal-img {
  margin-right: 19px;
}

.journal-info {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.journal-header {
  display: flex;
  align-items: center;
  margin-bottom: 7px;
}

.journal-country,
.google-link,
.journal-date,
.journal-text {
  font-size: 10.24px;
}

.journal-country,
.google-link,
.journal-text {
  font-weight: 400;
}

.journal-country {
  padding: 0 12px 0 4px;
  letter-spacing: 0.17em;
}

.google-link {
  color: #918E9B;
}

.journal-title,
.journal-date {
  font-weight: 700;
}

.journal-title {
  font-size: 25px;
  margin-bottom: 17px;
}

.journal-date {
  margin-bottom: 9px;
}

.journal-text {
  line-height: 150%;
}